const mdLinks = [
  `![](http://domain.com/foo "xxx")`,
  `![](http://domain.com/foo)`,
  `[](http://domain.com/foo)`,
  `[xxx](http://domain.com/foo)`,
  "[https](https://www.ibm.com)[mailto](mailto:someone@ibm.com) ",
];

const invalidMdLinks = [
  "[](http://domain.com/foo )",
  `[](http://domain.com/foo abc)`,
];

describe('Test linkify', () => {
  describe('Test valid link', () => {
    const linkify = createLinkify(['http:']);

    for (const link of [...valid, ...mdLinks, ...invalidMdLinks]) {
      it(`test ${link}`, () => {
        expect(linkify.test(link)).toBeTruthy();
      })
    }
  });

  describe('Test invalid link', () => {
    const linkify = createLinkify(['http:']);

    for (const link of invalid) {
      it(`test ${link}`, () => {
        expect(linkify.test(link)).toBeFalsy();
      })
    }
  });

  it('Test memorize', () => {
    const linkify1 = createLinkify(['http:']);
    const linkify2 = createLinkify(['http:']);
    const linkify3 = createLinkify(['https:']);
    const linkify4 = createLinkify(['https:', 'http:']);
    const linkify5 = createLinkify(['http:', 'https:']);
    expect(linkify1).toEqual(linkify2);
    expect(linkify1).not.toEqual(linkify3);
    expect(linkify4).toEqual(linkify5);

  });

  describe('matchesLink with markdown', () => {
    describe('valid link with markdown', () => {
      const linkify = createLinkify(['http:']);

      for (const link of [...valid, ...invalidMdLinks]) {
        it(`test ${link}`, () => {
          expect(matchesLink(linkify, link)).toBeTruthy();
        })
      }
    });

    describe('invalid link with markdown', () => {
      const linkify = createLinkify(['http:']);


      for (const link of [...invalid, ...mdLinks]) {
        it(`test ${link}`, () => {
          expect(matchesLink(linkify, link)).toBeFalsy();
        })
      }
    });
  })

});
